<template>
  <div>
    <div class="add-search-term">
      <label for="add-search-term" style="margin-right: 10px">Enter search term:</label>
      <input type="text" id="add-search-term" v-model="text" style="margin-right: 10px">
      <button v-on:click="addSearchTerm">Submit</button>
    </div>
    <hr>
    <h1>Search Results</h1>
    <hr>
    <p class="error" v-if="error">{{ error }}</p>
    <div>
      <div
        class="result"
        v-for="(result, index) in results"
        v-bind:item="result"
        v-bind:index="index"
        v-bind:key="result._id"
      >
        <h2 class="result-title">{{result.country}} : {{result.title}}</h2>
        <a class="result-link" v-bind:href="result.link">{{result.link}}</a>
      </div>
    </div>
  </div>
</template>

<script>
import ResultService from "../ResultService";

export default {
  name: "ResultComponent",
  data() {
    return {
      results: [],
      error: "",
      text: ""
    };
  },
  async created() {
    try {
      this.results = await ResultService.getResults();
    } catch (err) {
      this.error = err.message;
    }
  },
  methods: {
    async addSearchTerm() {
      await ResultService.addSearchTerm(this.text)
  }
  }
};
</script>

