import axios from 'axios';

const url = "/results"

class ResultService {
  // Static gives us access to the method without creating a new PostService object. i.e. we can just call PostService.getPosts()
  static getResults() {
    return new Promise( (resolve, reject) => {
      axios.get(url).then((res) => {
        const data = res.data;
        resolve(data)
      })
      .catch ((error)=> {
        reject(error)
      })
    })
  }
  static addSearchTerm(term) {
    return axios.post(url, {
      term
    })
  }
}

export default ResultService